'use client';

import { useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/Card';
import { Avatar } from '@/components/ui/Avatar';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Mail, Phone, MapPin, Calendar, Save, Lock } from 'lucide-react';

export default function StudentProfile() {
  const { user } = useAuth();
  const [isEditingProfile, setIsEditingProfile] = useState(false);
  const [isSavingPreferences, setIsSavingPreferences] = useState(false);
  const [preferences, setPreferences] = useState({
    emailNotifications: true,
    deadlineReminders: true,
    marketingEmails: false,
  });

  const handleEditProfile = () => {
    setIsEditingProfile(true);
  };

  const handleChangePassword = () => {
    // Could open a modal or navigate to change password page
    console.log('Open change password dialog');
  };

  const handleSavePreferences = async () => {
    setIsSavingPreferences(true);
    try {
      // Simulate saving preferences
      await new Promise(resolve => setTimeout(resolve, 800));
      setIsSavingPreferences(false);
      console.log('Preferences saved:', preferences);
    } catch (error) {
      setIsSavingPreferences(false);
      console.error('Failed to save preferences:', error);
    }
  };

  const togglePreference = (key: keyof typeof preferences) => {
    setPreferences(prev => ({
      ...prev,
      [key]: !prev[key],
    }));
  };

  return (
    <div className="space-y-6 max-w-2xl">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">My Profile</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-2">
          View and manage your account information
        </p>
      </div>

      {/* Profile Overview */}
      <Card>
        <CardHeader>
          <CardTitle>Profile Information</CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="flex items-start gap-6">
            <Avatar src={user?.avatar} name={user?.name} size="lg" />
            <div>
              <h3 className="text-2xl font-bold text-gray-900 dark:text-white">{user?.name}</h3>
              <p className="text-gray-600 dark:text-gray-400">{user?.email}</p>
              <div className="mt-4 flex gap-4">
                <Button variant="primary" onClick={handleEditProfile}>Edit Profile</Button>
                <Button variant="outline" onClick={handleChangePassword}>Change Password</Button>
              </div>
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4 pt-6 border-t border-gray-200 dark:border-gray-700">
            <div className="flex items-center gap-3">
              <Calendar className="w-5 h-5 text-gray-400" />
              <div>
                <p className="text-xs text-gray-600 dark:text-gray-400">Student ID</p>
                <p className="font-semibold text-gray-900 dark:text-white">CST-20210001</p>
              </div>
            </div>
            <div className="flex items-center gap-3">
              <MapPin className="w-5 h-5 text-gray-400" />
              <div>
                <p className="text-xs text-gray-600 dark:text-gray-400">Program</p>
                <p className="font-semibold text-gray-900 dark:text-white">BSIT</p>
              </div>
            </div>
            <div className="flex items-center gap-3">
              <Mail className="w-5 h-5 text-gray-400" />
              <div>
                <p className="text-xs text-gray-600 dark:text-gray-400">Email</p>
                <p className="font-semibold text-gray-900 dark:text-white">{user?.email}</p>
              </div>
            </div>
            <div className="flex items-center gap-3">
              <Phone className="w-5 h-5 text-gray-400" />
              <div>
                <p className="text-xs text-gray-600 dark:text-gray-400">Phone</p>
                <p className="font-semibold text-gray-900 dark:text-white">+63 9XX XXX XXXX</p>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Preferences */}
      <Card>
        <CardHeader>
          <CardTitle>Preferences</CardTitle>
          <CardDescription>Manage your notification and privacy settings</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700/50 rounded-lg">
            <div>
              <p className="font-medium text-gray-900 dark:text-white">Email Notifications</p>
              <p className="text-sm text-gray-600 dark:text-gray-400">Receive updates about evaluations</p>
            </div>
            <input 
              type="checkbox" 
              className="w-5 h-5 cursor-pointer" 
              checked={preferences.emailNotifications}
              onChange={() => togglePreference('emailNotifications')}
            />
          </div>

          <div className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700/50 rounded-lg">
            <div>
              <p className="font-medium text-gray-900 dark:text-white">Deadline Reminders</p>
              <p className="text-sm text-gray-600 dark:text-gray-400">Get reminders before deadlines</p>
            </div>
            <input 
              type="checkbox" 
              className="w-5 h-5 cursor-pointer" 
              checked={preferences.deadlineReminders}
              onChange={() => togglePreference('deadlineReminders')}
            />
          </div>

          <div className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700/50 rounded-lg">
            <div>
              <p className="font-medium text-gray-900 dark:text-white">Marketing Emails</p>
              <p className="text-sm text-gray-600 dark:text-gray-400">Receive updates and announcements</p>
            </div>
            <input 
              type="checkbox" 
              className="w-5 h-5 cursor-pointer" 
              checked={preferences.marketingEmails}
              onChange={() => togglePreference('marketingEmails')}
            />
          </div>

          <div className="pt-4">
            <Button 
              variant="primary" 
              onClick={handleSavePreferences}
              disabled={isSavingPreferences}
            >
              {isSavingPreferences ? 'Saving...' : 'Save Preferences'}
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
